import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from "react-router-dom";

const Header = () => {
  return (
    <div className="jumbotron jumbotron-fluid">
      <div className="container">
        <div className="jumbotron-bg"></div>
        <h1 className="display-4">Welcome to MainShop</h1>
        <p className="lead">You will find here everything what You need</p>
        <Link className="btn btn-lg btn-primary" to="/shop">
          Check what you can get !
        </Link>
      </div>
    </div>
  );
};

export default Header;
