import React from "react";
import Item from "./Item";
import Pagination from "./Pagination";

const ItemsShowList = () => {
  return (
    <div className="col-md-9">
      <h2 className="mb-4">Products</h2>
      <Pagination />
      <div className="row">
        <Item />
      </div>
    </div>
  );
};

export default ItemsShowList;
