import React, { createContext,  useEffect, useState } from 'react';

export const StoreContext = createContext(null)

const StoreProvider = ({children}) => {
    const [items, setItems] = useState();
    const [users, setUsers ] = useState();


    useEffect(()=>{ 
        fetch('./db/items.json')
            .then(res => (res.json()))
            .then(data => setItems(data.itemsData))
            .catch(err=> console.log('błąd przy pobieraniu danych o produktach' , err))
     },[])

     useEffect(()=>{ 
        fetch('./db/users.json')
            .then(res => (res.json()))
            .then(data => setUsers(data.itemsData))
            .catch(err=> console.log('błąd przy pobieraniu danych o użytkownikach' , err))
     },[])


    return (
        <StoreContext.Provider value ={ {
            items, 
            setItems ,
            users,
            setUsers,
            }}>
            {children}
        </StoreContext.Provider>
    )
};

export default StoreProvider;